/***

====================================================================
			Fonts
====================================================================

 ***/

 @import url('https://fonts.googleapis.com/css?family=Arimo:400,400i,700,700i|Raleway:300,300i,400,400i,500,500i,600,600i,700,700i,800,800i,900,900i');

 /*
 font-family: 'Raleway', sans-serif;
 font-family: 'Arimo', sans-serif;
 */
 
 @import url('flaticon.css');
 @import url('line-awesome.css');
 @import url('animate.css');
 @import url('owl.css');
 @import url('jquery-ui.css');
 @import url('jquery.fancybox.min.css');
 
 /***
 
 ====================================================================
	 Reset
 ====================================================================
 
	***/
 
 * {
	 margin: 0px;
	 padding: 0px;
	 border: none;
	 outline: none;
	 font-size: 100%;
 }
 
 /***
 
 ====================================================================
	 Global Settings
 ====================================================================
 
	***/
 
 body {
 
	 font-size: 14px;
	 color: #777777;
	 line-height: 2em;
	 font-weight: 400;
	 background: #ffffff;
	 -webkit-font-smoothing: antialiased;
	 -moz-font-smoothing: antialiased;
	 font-family: 'Arimo', sans-serif;
 }
 
 a {
	 text-decoration: none;
	 cursor: pointer;
	 color: #126977;
 }
 
 a:hover,
 a:focus,
 a:visited {
	 text-decoration: none;
	 outline: none;
 }
 
 h1, h2, h3, h4, h5, h6 {
	 position: relative;
	 font-weight: normal;
	 margin: 0px;
	 background: none;
	 line-height: 1.2em;
	 font-family: 'Raleway', sans-serif;
 }
 
 textarea {
	 overflow: hidden;
	 resize: none;
 }
 
 button {
	 outline: none !important;
	 cursor: pointer;
 }
 
 p, .text {
	 font-size: 16px;
	 line-height: 26px;
	 font-weight: 400;
	 color: #555555;
	 margin: 0;
 }
 
 ::-webkit-input-placeholder {
	 color: inherit;
 }
 
 ::-moz-input-placeholder {
	 color: inherit;
 }
 
 ::-ms-input-placeholder {
	 color: inherit;
 }
 
 .auto-container {
	 position: static;
	 max-width: 1200px;
	 padding: 0px 15px;
	 margin: 0 auto;
 }
 
 .page-wrapper {
	 position: relative;
	 margin: 0 auto;
	 width: 100%;
	 min-width: 300px;
	 overflow: hidden;
	 z-index: 99;
	 min-height: 100vh;
 }
 
 .pageWrap {
	 min-height: 100vh;
 }
 
 ul, li {
	 list-style: none;
	 padding: 0px;
	 margin: 0px;
 }
 
 img {
	 object-fit: cover;
 }
 
 .theme-btn {
	 display: inline-block;
	 -webkit-transition: all 400ms ease;
	 -moz-transition: all 400ms ease;
	 -ms-transition: all 400ms ease;
	 -o-transition: all 400ms ease;
	 transition: all 400ms ease;
 }
 
 .centered {
	 text-align: center !important;
 }
 
 /*Btn Style One*/
 
 .btn-style-one {
	 position: relative;
	 font-size: 14px;
	 line-height: 20px;
	 color: #ffffff;
	 padding: 15px 25px;
	 font-weight: 700;
	 background-color: #126977;
	 text-transform: uppercase;
	 -webkit-box-shadow: rgba(34, 34, 34, 0) 0 0px 0px 0px inset;
	 -moz-box-shadow: rgba(34, 34, 34, 0) 0 0px 0px 0px inset;
	 -ms-box-shadow: rgba(34, 34, 34, 0) 0 0px 0px 0px inset;
	 -o-box-shadow: rgba(34, 34, 34, 0) 0 0px 0px 0px inset;
	 box-shadow: rgba(34, 34, 34, 0) 0 0px 0px 0px inset;
 }
 
 .btn-style-one:hover {
	 color: #ffffff;
	 -webkit-box-shadow: rgba(34, 34, 34, 1) 0 0px 0px 40px inset;
	 -moz-box-shadow: rgba(34, 34, 34, 1) 0 0px 0px 40px inset;
	 -ms-box-shadow: rgba(34, 34, 34, 1) 0 0px 0px 40px inset;
	 -o-box-shadow: rgba(34, 34, 34, 1) 0 0px 0px 40px inset;
	 box-shadow: rgba(34, 34, 34, 1) 0 0px 0px 40px inset;
 }
 
