 }
 
 .list-style-one {
	 position: relative;
	 display: block;
 }
 
 .list-style-one li {
	 position: relative;
	 font-size: 15px;
	 line-height: 26px;
	 color: #666666;
	 font-weight: 400;
	 margin-bottom: 10px;
	 padding-left: 35px;
 }
 
 .list-style-one li:before {
	 position: absolute;
	 left: 0;
	 top: 0;
	 font-size: 18px;
	 line-height: 25px;
	 color: #126977;
	 font-weight: 400;
	 font-family: "LineAwesome";
	 content: "\f17d";
 }
 
 .theme_color {
	 color: #25262c;
 }
 
 .pull-right {
	 float: right;
 }
 
 .pull-left {
	 float: left;
 }
 
 .preloader {
	 position: fixed;
	 left: 0px;
	 top: 0px;
	 width: 100%;
	 height: 100%;
	 z-index: 999999;
	 background-color: #ffffff;
	 background-position: center center;
	 background-repeat: no-repeat;
	 background-image: url(/images/icons/preloader.gif);
 }
 
 img {
	 display: inline-block;
	 max-width: 100%;
	 height: auto;
 }
 
 /***
 
 ====================================================================
	 Scroll To Top style
 ====================================================================
 
 ***/
 
 .scroll-to-top {
	 position: absolute;
	 top: 0;
	 left: 50%;
	 margin-left: -15px;
	 margin-top: -15px;
	 width: 30px;
	 height: 30px;
	 color: #ffffff;
	 font-size: 14px;
	 line-height: 30px;
	 text-align: center;
	 z-index: 100;
	 cursor: pointer;
	 background: #126977;
	 display: none;
	 -webkit-transition: all 300ms ease;
	 -ms-transition: all 300ms ease;
	 -o-transition: all 300ms ease;
	 -moz-transition: all 300ms ease;
	 transition: all 300ms ease;
 }
 
 .scroll-to-top:hover {
	 color: #126977;
	 background: #ffffff;
 }
 
 /***
 
 ====================================================================
	 Main Header style
 ====================================================================
 
 ***/
 
 .main-header {
	 position: relative;
	 width: 100%;
	 z-index: 9999;
 }
 
 .main-header .header-top {
	 position: relative;
	 background-color: #222222;
 }
 
 .main-header .top-left {
	 position: relative;
	 float: left;
 }
 
 .main-header .top-left .contact-list {
	 position: relative;
	 padding: 25px 0;
 }
 
 .main-header .top-left .contact-list li {
	 position: relative;
	 float: left;
	 font-size: 14px;
	 line-height: 25px;
	 color: #e9e8e9;
	 font-weight: 400;
	 margin-right: 25px;
	 font-family: "Arimo", sans-serif;
 }
 
 .main-header .top-left .contact-list li:last-child {
	 margin-right: 0;
 }
 
 .main-header .top-left .contact-list li i {
	 float: left;
	 font-size: 24px;
	 line-height: 25px;
	 margin-right: 20px;
	 color: #126977;
 }
 
 .main-header .top-left .contact-list li a {
	 color: #e9e8e9;
	 display: inline-block;
	 -webkit-transition: all 300ms ease;
	 -moz-transition: all 300ms ease;
	 -ms-transition: all 300ms ease;
	 -o-transition: all 300ms ease;
	 transition: all 300ms ease;
 }
 
 .main-header .top-left .contact-list li a:hover {
	 color: #126977;
 }
 
 .main-header .top-right {
	 position: relative;
	 float: right;
	 padding: 25px 0;
 }
 
 .social-icon-one {
	 position: relative;
 }
 
 .social-icon-one li {
	 position: relative;
	 float: left;
	 margin-left: 25px;
 }
 
 .social-icon-one li:first-child {
	 margin-left: 0;
 }
 
 .social-icon-one li a {
	 position: relative;
	 display: block;
	 font-size: 20px;
	 line-height: 25px;
	 color: #e8e8e8;
	 vertical-align: bottom;
	 -webkit-transition: all 300ms ease;
	 -moz-transition: all 300ms ease;
	 -ms-transition: all 300ms ease;
	 -o-transition: all 300ms ease;
	 transition: all 300ms ease;
 }
 
 .social-icon-one li a:hover {
	 color: #126977;
 }
 
 /*=== Header Lower ===*/
 
 .main-header .header-lower {
	 position: relative;
	 display: none;
 }
 
 .main-header .header-lower .inner-container {
