 .social-button {
	 position: fixed;
	 right: 13px;
	 bottom: 30px;
	 transform: translate(-50%, -50%);
	 background-color: #25d366;
	 box-shadow: 2px 2px 3px #0009;
	 /*цвет кнопки*/
	 border-radius: 50%;
	 width: 55px;
	 /*ширина кнопки*/
	 height: 55px;
	 /*высота кнопки*/
	 color: #fff;
	 text-align: center;
	 line-height: 56px;
	 /*центровка иконки в кнопке*/
	 font-size: 35px;
	 /*размер иконки*/
	 z-index: 9999;
	 padding-left: 2px;
 }
 
 .social-button a {
	 color: #fff;
 }
 
 .social-button:before,
 .social-button:after {
	 content: " ";
	 display: block;
	 position: absolute;
	 border: 50%;
	 border: 1px solid #25D366;
	 /*цвет анимированных волн от кнопки*/
	 left: -20px;
	 right: -20px;
	 top: -20px;
	 bottom: -20px;
	 border-radius: 50%;
	 animation: animate 1.5s linear infinite;
	 opacity: 0;
	 backface-visibility: hidden;
 }
 
 .social-button:after {
	 animation-delay: .5s;
 }
 
