 @keyframes animate {
	 0% {
		 transform: scale(0.5);
		 opacity: 0;
	 }
 
	 50% {
		 opacity: 1;
	 }
 
	 100% {
		 transform: scale(1);
		 opacity: 0;
	 }
 }
 
